class NewPlaylistSnackbar extends React.Component {
	constructor(props) {
    	super(props);
    	this.state = {
      		autoHideDuration: 4000,
      		message: 'Playlist Created',
      		open: false,
    	};
  	}

  handleTouchTap = () => {
    this.setState({
      open: true,
    });
  };

  handleActionTouchTap = () => {
    this.setState({
      open: false,
    });
    console.log('Event removed from your calendar.');
  };

  handleChangeDuration = (event) => {
    const value = event.target.value;
    this.setState({
      autoHideDuration: value.length > 0 ? parseInt(value, 10) : 0,
    });
  };

  handleRequestClose = () => {
    this.setState({
      open: false,
    });
  };

	render() {
		return (
				<span>
					 <FlatButton
              onTouchTap={this.handleTouchTap}
							primary
              label="CREATE"
                        />
            <Snackbar
              open={this.state.open}
              message={this.state.message}
              action="undo"
              autoHideDuration={this.state.autoHideDuration}
              onActionTouchTap={this.handleActionTouchTap}
              onRequestClose={this.handleRequestClose}
                        />
				</span>

		)
	}
}

class NewPlaylistDialog extends React.Component {
	state = {
		characters: characterLength,
    	open: false,
  	};
	// constructor(props) {
	//     super(props);
	//     this.state = {
    //   		characters: characterLength,
	// 		open: false
	//     };
    // 	}

  	handleOpen = () => {
    	this.setState({open: true});
  	};

 //  	handleClose = () => {
    // 	this.setState({open: false});
 //  	};

  	handleCharacters = (event, value) =>{
		var input = event.target.value;
		this.setState({characters: characterLength - input.length});
	}

  	handleChange = (event, index, value) => this.setState({value});

	handleRequestClose = () => this.setState({ open: false });

  	render() {
    	// const actions = [
		// 	<Button
		// 		onTouchTap={this.handleRequestClose}
		// 		primary>
		// 		Cancel
		// 	</Button>,
     //  		<NewPlaylistSnackbar/>,
    	// ];

    	return (
			<span>
				<Button fab primary onTouchTap={this.handleOpen}>
					<AddIcon />
				</Button>
            	<Dialog
					open={this.state.open}
          			onRequestClose={this.handleRequestClose}>
					<DialogTitle>{'Create a New Playlist'}</DialogTitle>
					<DialogContent>
	            		<DialogContentText>
							<TextField
								id="name"
		          				label="Name"
		          				value=""/>
							<br />
							<TextField
								id="description"
								label="Description"
								value=""
								onChange={this.handleCharacters.bind(this)}/>
							<br />
							<span>{this.state.characters} Characters remaining</span>
							<br/>
							{/*<SelectField
								floatingLabelText="Media Type"
								value={this.state.value}
								onChange={this.handleChange}>

								<MenuItem value={1} primaryText="Media" />
								<MenuItem value={2} primaryText="Audio" />
								<MenuItem value={3} primaryText="Data" />
							</SelectField>
							<br />*/}
	            		</DialogContentText>
	          		</DialogContent>
					<DialogActions>
            			<Button onTouchTap={this.handleRequestClose} primary>Cancel</Button>
          			</DialogActions>
          		</Dialog>
          </span>
    	);
  	}
}

