class Main extends React.Component {

	state = {
	    index: 0,
    	anchorEl: undefined,
    	open: false,
	    checkedA: true,
	    checkedB: false,
	    checkedC: false,
  	};

	button = undefined;

	handleChange = (event, index) => {
		this.setState({ index });
	};

	handleChangeIndex = (index) => {
		this.setState({ index });
	};

	handleClick = (event) => {
    	this.setState({ open: true, anchorEl: event.currentTarget });
  	};

  	handleRequestClose = () => {
    	this.setState({ open: false });
  	};

	constructor(props) {
    	super(props);

    	this.state = {
			fixedHeader: true,
			fixedFooter: false,
			stripedRows: false,
			showRowHover: false,
			selectable: true,
			multiSelectable: false,
			enableSelectAll: false,
			deselectOnClickaway: true,
			showCheckboxes: false,
			height: '500px',
    	};
  	}

	// handleToggle = (event, toggled) => {
    // 	this.setState({
    //   		[event.target.name]: toggled,
    // 	});
 //  	};

   	getInitialState= () => {
        return { showResults: false };
    };

	toList= () => {
		window.location.href= "#/app/playlist/listItems";
	};


  	render() {
	 	const { playlistHealthy, playlistType } = this.props;
        let healthIcon = null;
        if (!playlistHealthy) {
            healthIcon = <i className="material-icons">fitness_center</i>;
        } else {
            healthIcon = <i className="material-icons">warning</i>;
        }
    	return (
      		<article className="article">
        		<div className="row">
          			<div className="col-xl-9">
            			<Table
							height={this.state.height}
							fixedHeader={this.state.fixedHeader}
							fixedFooter={this.state.fixedFooter}
							selectable={this.state.selectable}
							multiSelectable={this.state.multiSelectable}>
              				<TableHead
				                displaySelectAll={this.state.showCheckboxes}
				                adjustForCheckbox={this.state.showCheckboxes}
				                enableSelectAll={this.state.enableSelectAll}>

                				<TableRow>
              						<TableCell tooltip="The ID">ID</TableCell>
					              	<TableCell tooltip="The Name">Playlist Type</TableCell>
					              	<TableCell tooltip="The Status">Playlist Name</TableCell>
									<TableCell>Playlist Health</TableCell>
									<TableCell>Total Items</TableCell>
									<TableCell tooltip="Click for more Info">Details</TableCell>
									<TableCell>Actions</TableCell>
                				</TableRow>
              				</TableHead>
              				<TableBody
					            displayRowCheckbox={this.state.showCheckboxes}
					            deselectOnClickaway={this.state.deselectOnClickaway}
					            showRowHover={this.state.showRowHover}
					            stripedRows={this.state.stripedRows}>
                				{tableData.map((row, index) => (
                  				<TableRow key={index} selected={row.selected}>
                    				<TableCell>{index}</TableCell>
									<TableCell>{row.playlistType}</TableCell>
				                    <TableCell>{row.name}</TableCell>
				                    <TableCell>{healthIcon}</TableCell>
									<TableCell>5</TableCell>
									<TableCell>
										<Button
							          		aria-owns="simple-menu"
							          		aria-haspopup="true"
							          		onClick={this.handleClick}>
							          		<i class="material-icons">info_outline</i>
								        </Button>
										<Menu
			      							id="simple-menu"
			      							anchorEl={this.state.anchorEl}
			      							open={this.state.open}
			      							onRequestClose={this.handleRequestClose}>
											<MenuItem onClick={this.handleRequestClose}
												className={classnames('menu-item', {
													'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
													'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
												})}>
												Duration:
												{row.prettifyDuration}
											</MenuItem>
											<MenuItem onClick={this.handleRequestClose}
												className={classnames('menu-item', {
													'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
													'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
												})}>
												Last Modified:
												{row.lastModified}
											</MenuItem>
										</Menu>
									</TableCell>
									<TableCell>
										<Button
							          		aria-owns="simple-menu"
							          		aria-haspopup="true"
							          		onClick={this.handleClick}>
							          		<i class="material-icons">info</i>
								        </Button>
										<Menu
			      							id="simple-menu"
			      							anchorEl={this.state.anchorEl}
			      							open={this.state.open}
			      							onRequestClose={this.handleRequestClose}>
											<MenuItem onClick={this.handleRequestClose}
												className={classnames('menu-item', {
													'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
													'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
												})}>
												<i className="material-icons">control_point_duplicate</i>
											 	Duplicate Playlist
											</MenuItem>
											<MenuItem onClick={this.handleRequestClose}
												className={classnames('menu-item', {
													'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
													'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
												})}>
												<i className="material-icons">delete_forever</i>
									 			Delete Playlist
											</MenuItem>
										</Menu>
									</TableCell>

                  				</TableRow>
                			))}
              				</TableBody>
            			</Table>
          			</div>
          			<div id="table-filters" className="col-xl-3">
						<FilterIcon style={{color: "white"}}/>
        				<h6 style={{display: "inline-block", position: "relative", bottom: "6px", left: "15px"}}>FILTERS</h6>
						<Switch
							label="Data Playlist"
	          				checked={this.state.checkedA}
	          				onChange={(event, checked) => this.setState({ checkedA: checked })}
	          				aria-label="checkedA"/>
						<Switch
							label="Playlist (regular)"
	          				checked={this.state.checkedB}
	          				onChange={(event, checked) => this.setState({ checkedB: checked })}
	          				aria-label="checkedB"/>
						<Switch
							label="Playlist (smart)"
	          				checked={this.state.checkedC}
	          				onChange={(event, checked) => this.setState({ checkedC: checked })}
	          				aria-label="checkedC"/>
          			</div>
        		</div>
      		</article>
    	);
  	}
}
